* {
  box-sizing: border-box;
  color: #000;
  background: #fff;
  margin: 0;
  padding: 0;
}

.game {
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.title {
  z-index: 2;
  font-size: 4rem;
  position: absolute;
  top: 0;
}

.score {
  width: 30vw;
  z-index: 1;
  justify-content: space-evenly;
  display: flex;
  position: absolute;
  top: 70px;
}

.p-count, .c-count {
  text-align: center;
  margin-top: 1rem;
  font-size: 1.5rem;
}

.options {
  width: 50vw;
  justify-content: space-evenly;
  margin-top: 2rem;
  display: flex;
}

.rock, .paper, .scissor {
  width: 100px;
  cursor: pointer;
  background: red;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: .8rem;
}

.move {
  font-size: 2rem;
  font-weight: bold;
}

.reload {
  cursor: pointer;
  background: red;
  border: none;
  border-radius: 10px;
  outline: none;
  margin-top: 2rem;
  padding: 1rem;
  display: none;
}

.result {
  margin-top: 20px;
  font-size: 1.2rem;
}

@media screen and (max-width: 612px) {
  .title {
    text-align: center;
  }

  .score {
    width: 100vw;
    position: absolute;
    top: 200px;
  }

  .options {
    width: 100vw;
  }
}

